<template>
  <div id="app">
    <!-- 路由匹配到的组件将显示在这里 -->
    <router-view/>
  </div>
</template>

<script>
  /*把app导出为一个组件这个组件供main.js加载这是个集合形式的组件。*/
  export default {
    name: 'App'
  }
</script>

